/* ==================================Независимый контейнер Заголовка======================== */
* {
    box-sizing: border-box;
    font-family: "Roboto", Tahoma;
}

.main-title {
    display: flex;
    height: 100px;
    justify-content: center;
    font-size: 55px;
    background-color: rgb(236, 236, 236);
}

/* ==================================Главный контейнер НОВОСТЕЙ и ВИДЖЕТА======================== */
.common-backdrop {
    display: flex;
    justify-content: center;
    background-color: rgb(236, 236, 236);
    width: 100vw;
    gap: 0px 20px;
    /* ==========del&all============ */
}

/* ==================================контейнер Новости======================== */
.news {
    display: flex;
    width: 600px;
    /* flex-flow: row wrap; */
    flex-direction: column;
    justify-content: center;
    gap: 30px;
}

.one-news-container {
    top: 50px;
    background-color: rgb(255, 255, 255);
    padding: 20px;
    border-radius: 10px;
}

.author-text {
    font-size: 18px;
    font-weight: bold;
    border-bottom: 2px solid #000;
}

.title-text {
    font-size: 22px;
    font-weight: bold;
    padding: 15px 0px 15px 0px;
}

.news-text-conteiner{
    font-size: 17px !important;
}
/* ==================================Конец Новости======================== */
/* ================================== Виджитов======================== */
.author {
    position: sticky;
    top: 15px;
    background-color: rgb(252, 250, 248);
    height: 90vh;
    width: 200px;
    border-radius: 10px;
    font-size: 16px;
    
    

    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.news-authors {
    font-size: 15px !important;
    border-bottom: 0px solid black !important;
}

.link {
    font-size: 16px;
    color: #000 !important;
}
/* ================================== Виджитов======================== */
.loading {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 200px;
    width: 200px;
    border: 4px solid rgb(238, 7, 7);
    height: 100vh;
    box-sizing: border-box;

}