body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'Caveat-Regular';
  src: url('/public/fonts/caveat/Caveat-Regular.ttf') format('truetype');
}

@font-face {
  font-family: 'Caveat-Bold';
  src: url('/public/fonts/caveat/Caveat-Bold.ttf') format('truetype');
}

@font-face {
  font-family: 'BERNIERDistressed';
  src: url('/public/fonts/berniershadeHeder/BERNIERDistressed-Regular.otf') format('opentype');
}

@font-face {
  font-family: 'BERNIERRegular';
  src: url('/public/fonts/berniershadeHeder/BERNIERRegular-Regular.otf') format('opentype');
}

@font-face {
  font-family: 'BERNIERShade-Regular';
  src: url('/public/fonts/berniershadeHeder/BERNIERShade-Regular.otf') format('opentype');
}